
body {
  margin: 0;
  text-align: center;
  font-family: -apple-system, BlinkMacSystemFont,
             'avenir next', avenir,
             helvetica, 'helvetica neue',
             Ubuntu,
             'segoe ui', arial,
             sans-serif;
}
body * {
  font-weight: 300;
}
.users {
  max-width: 400px;
  margin: 0 auto;
  background-color: #f0f0f0;
}
.users .user {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #e2e2e2;
}
.users .user .avatar {
  margin-right: 10px;
}
.mugen-scroll {
  padding: 10px;
  font-size: 18px;
}
.count {
  position: fixed;
  bottom: 10px;
  right: 10px;
  font-size: 18px;
}



